import React from 'react';

const Option = ({ char, id, classname, handleSelection }) => {
    return (
        <div className={'individualOptions ' + classname}>
            <label htmlFor={id}>{char}</label>
            <input type="radio" id={id} name='option' value={char} onChange={handleSelection}/><br />
        </div>
    );
}


export default Option;
