import React, { Component } from 'react';
import {Animated} from "react-animated-css";
import Options from './Options';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentQuote: [],
      currentCharacter: [],
      cardStatus: 'hide'
    }

    this.urls = [
      'https://the-one-api.dev/v2/quote/',
      'https://the-one-api.dev/v2/character/'
    ]

    this.allCharacters = JSON.parse(localStorage.getItem('allCharacters'));;
    this.allQuotes = JSON.parse(localStorage.getItem('allQuotes'));
    this.allQuotesChars = JSON.parse(localStorage.getItem('allQuotesChars'));
    this.correctAnswers = 0;
    this.numberOfQuestions = 0;
    this.currentSelection = '';
    this.optionSelected = false;
    this.correctAnswer = false;
    this.resultText = '';
    this.selectedOption = '';
  }

  async componentDidMount() {
    await this.fetchQuotes();
    await this.fetchAllCharacters();
  }

  updateStoredData = () => {
    this.allCharacters = JSON.parse(localStorage.getItem('allCharacters'));
    this.allQuotes = JSON.parse(localStorage.getItem('allQuotes'));
    this.allQuotesChars = JSON.parse(localStorage.getItem('allQuotesChars'));
  }

  async fetchQuotes() {
    const resp = await fetch(this.urls[0], {
      headers: {
        Authorization: "Bearer Ye6ZHvvDJkrLbHVjNtK4"
      }
    });
    const quotes = await resp.json()
    localStorage.setItem('allQuotes', JSON.stringify(quotes.docs));
    this.updateStoredData();
  }

  async fetchAllCharacters() {
    const resp = await fetch(this.urls[1], {
      headers: {
        Authorization: "Bearer Ye6ZHvvDJkrLbHVjNtK4"
      }
    });
    const characters = await resp.json();
    localStorage.setItem('allCharacters', JSON.stringify(characters.docs));
    this.updateStoredData();
    this.getAllCharsFromQuotes();
  }

  updateQuote = () => {
    this.setState({ cardStatus: 'makeOptionsTransparent' });
    let randQuote = this.allQuotes[Math.floor(Math.random() * (this.allQuotes.length-0) + 0)];
    while (randQuote.dialog.length < 20) {
      console.log('too short');
      randQuote = this.allQuotes[Math.floor(Math.random() * (this.allQuotes.length-0) + 0)];
    }
    this.allCharacters.forEach(c => {
      if (c._id === randQuote.character) {
        this.setState({
          currentQuote: randQuote,
          currentCharacter: c,
          cardStatus: 'show'
        });
        return;
      }
    })
  }

  getAllCharsFromQuotes = () => {
    const allCharsFromQuotes = [];
    this.allQuotes.forEach(q => {
      this.allCharacters.forEach(c => {
        if (!allCharsFromQuotes.includes(c.name) && (q.character === c._id)) {
          allCharsFromQuotes.push(c.name);
        }
      })
    })
    localStorage.setItem('allQuotesChars', JSON.stringify(allCharsFromQuotes));
    this.updateStoredData();
  }

  handleSelection = (e) => {
    this.optionSelected = true;
    this.currentSelection = e.target.value;
    this.selectedOption = e.target;
  }

  handleSubmit = () => {
    if (this.optionSelected || this.state.cardStatus === 'hide') {
      if (this.currentSelection === this.state.currentCharacter.name) {
        this.correctAnswers += 1;
        this.resultText = 'Correct!'
        this.correctAnswer = true;
        this.numberOfQuestions += 1;
        this.selectedOption.checked = false;
      } else if (this.state.cardStatus === 'show') {
        this.resultText = 'Correct Answer: ' + this.state.currentCharacter.name;
        this.correctAnswer = false;
        this.numberOfQuestions += 1;
        this.selectedOption.checked = false;
      }
      this.updateQuote();
      this.optionSelected = false;
    }
  }

  buttonContent = () => {
    if (this.state.cardStatus === 'show') {
      return 'Submit'
    } else {return 'Start'}
  }

  render() {
    return (
      <div className='container'>
        <div></div>
        <Animated animationIn="" animationOut="" isVisible={true}>
          <div className='quote'><h4 className='quoteText'>{this.state.currentQuote.dialog}</h4></div>
        </Animated>
        <div className='score'><h3>{'Score: ' + this.correctAnswers + '/' + this.numberOfQuestions}</h3></div>
        <div className='options'>
          <h4 className='resultText' style = {{color: this.correctAnswer === true ? 'green' : 'red'}}>{this.resultText}</h4>
          <form className='allOptions'>
            <Options character={this.state.cardStatus === 'show' ? this.state.currentCharacter.name : null} characters={this.state.cardStatus === 'show' ? this.allQuotesChars : null} handleSelection={this.handleSelection} />
            <button onClick={this.handleSubmit} type='button'>{this.buttonContent()}</button>
          </form>
        </div>
        <div></div>
      </div>
    )
  }
}

export default App;

