import React from 'react';
import Option from './Option';

const getRandomCharacter = (characters) => {
    return Math.floor(Math.random() * (characters.length-0) + 0);
}

const Options = ({character, characters, handleSelection}) => {
    let randomCharacters = [];
    let index = 0;
    let answerPosition = Math.floor(Math.random() * (3-0) + 0);
    for (let i = 0; i < 4; i++) {
        if (characters !== null) {
            randomCharacters[i] = characters[getRandomCharacter(characters)];

            if (randomCharacters.slice(0,(randomCharacters.length - 1)).includes(randomCharacters[i]) || randomCharacters[i] === character) {
                i-=1;
            }
        }
    }

    return (
        randomCharacters.map(char => {
            index+=1;
            if (answerPosition === (index - 1)) {
                return <Option char={character} id={'option' + index} key={character} classname='answer' handleSelection={handleSelection} />
            } else {
                return <Option char={char} id={'option' + index} key={char} classname='' handleSelection={handleSelection} />
            }
        })
    )
}

export default Options;
